import { Component, h, Element } from '@stencil/core/internal';
import tailwind from '../../output.css';
import { data } from '../../data';
@Component({
  tag: 'rf-navbar',
  shadow: true,
})
export class Navbar {
  @Element() element: HTMLElement;

  hotel = data.My_Result;

  componentDidLoad() {
    const shadowRoot = this.element.shadowRoot;
    if (shadowRoot) {
      const style = document.createElement('style');
      style.textContent = tailwind;
      shadowRoot.appendChild(style);
    }
  }
  render() {
    return (
        <nav class="flex flex-row justify-between px-4 lg:px-20 xl:px-60 py-3 fixed top-0 left-0 w-full bg-white z-10">
        <div class='flex flex-col'>
          <div class='font-bold text-xl'>{this.hotel.name}</div>
          <small class='text-gray-600 text-base'>{this.hotel.area + ', ' + this.hotel.city.name + ', ' + this.hotel.country.name}</small>
        </div>
        <div class="hidden lg:flex flex-row gap-10 items-center text-gray-600 font-semibold">
          <button>Booking code</button>
          <button>$</button>
          <button>English</button>
          <button>Sign in</button>
        </div>
      </nav>
    );
  }
}
